.mainCont {
    background: rgb(14,14,142);
    overflow: hidden;
    width: 100%;
    height: 100vh;
    background: linear-gradient(90deg, rgba(14,14,142,1) 4%, rgba(203,0,255,1) 100%);
    .spinnerCont {
        width: 100%;
        height: 100vh;
        display: flex;
        align-items: center;
        position: absolute;
        z-index: 6;
        top: 0;
        left: 0;
        justify-content: center;
        color: #fff;
        opacity: 1;
        transition: ease-in;
        transition-duration: 1s;
        background: rgb(86,86,255);
        background: linear-gradient(90deg, rgba(86,86,255,1) 4%, rgba(203,0,255,1) 100%); }
    .spinnerContAnime {
        animation: opacityAnime 1s;
        animation-direction: reverse;
        opacity: 0;
        z-index: -1; }
    .cont {
        width: 100%;
        height: 100vh;
        position: relative;
        z-index: 0;
        padding: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        overflow: hidden;
        transition-duration: 3s;
        transition: ease-in;
        // opacity:
        // transform: scale(1.1)
        animation: contAnime 1s ease-in;
        .headerCont {
            position: relative;
            z-index: 3;
            width: 100%;
            display: flex;
            justify-content: flex-end;
            .headerButtonCont {
                display: flex;
                padding: 1em;
                height: min-content;
                transition: ease-in;
                // transition-duration: 1s
                opacity: 0; }
            .headerButtonAnime {
                animation: opacityAnime 1s;
                opacity: 1;
                .headerAboutUs {
                    font-size: 1.2em;
                    padding: 0.4em 0em;
                    margin-right: 1em;
                    // background-color: #fff
                    height: min-content;
                    cursor: pointer;
                    text-transform: capitalize;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-weight: bold;
                    border: 0.2em solid transparent;
                    color: #fff;
                    transition: ease-in;
                    transition-duration: 0.3s;
                    &:hover {
                        border-bottom: 0.2em solid #fff; } }
                .headerLetsgo {
                    text-transform: capitalize;
                    padding: 0.6em 1.3em;
                    font-weight: bold;
                    font-size: 1.2em;
                    margin-right: 1em;
                    background-color: #fff;
                    border-radius: 1.5em;
                    color: #0e0e8e;
                    cursor: pointer;
                    margin-right: 1em;
                    height: min-content;
                    transition: ease-in;
                    transition-duration: 0.3s;
                    &:hover {
                        margin-right: 0.3em;
                        padding: 0.6em 2em; } } } }
        .backgroundImg {
            position: absolute;
            top: 0;
            left: 0;
            object-fit: cover;
            width: 100%;
            height: 100%;
            z-index: 5;
            transition: ease-in;
            transition-duration: 1s; }
        .backgroundImgAnime {
            animation: background-animation 1s;
            z-index: 1; }
        .clouds {
            position: absolute;
            top: 0;
            left: 0;
            object-fit: cover;
            width: 100%;
            height: 100%;
            z-index: -1;
            transition: ease-in;
            transition-duration: 1s; }
        .cloudsAnime {
            z-index: 1;
            animation: clouds-animation 5s;
            animation-delay: 1s; }
        .way {
            position: absolute;
            bottom: 0;
            left: 0;
            object-fit: cover;
            width: 100%;
            height: 100%;
            z-index: 2;
            transition: ease-in;
            transition-duration: 1s; }
        .mosqueTowersCont {
            position: absolute;
            width: 40em;
            z-index: 1;
            right: 10em;
            height: 100%;
            .moon {
                position: absolute;
                bottom: 7em;
                object-fit: contain;
                width: 35em;
                margin: 0 2.5em;
                transition: ease-in;
                transition-duration: 1s;
                z-index: -2; }
            .moonAnime {
                z-index: 2;
                animation: moon-animation 9s;
                animation-delay: 1s; }
            .towers {
                position: absolute;
                object-fit: contain;
                width: 100%;
                transition: ease-in;
                transition-duration: 1s;
                bottom: 0;
                z-index: -2; }
            .towersAnime {
                z-index: 2;
                animation: towers-animation 7s;
                animation-delay: 1s; }
            .mosque {
                position: absolute;
                object-fit: contain;
                transition: ease-in;
                transition-duration: 1s;
                width: 27em;
                bottom: 0;
                margin: 0 6.5em;
                z-index: -2; }
            .mosqueAnime {
                z-index: 2;
                animation: mosque-animation 5s;
                animation-delay: 1s; } }
        .littleTowers {
            position: absolute;
            object-fit: contain;
            transition: ease-in;
            transition-duration: 1s;
            width: 70em;
            bottom: 0;
            right: 0;
            z-index: -2; }
        .littleTowersAnime {
            z-index: 2;
            animation: towers-animation 10s;
            animation-delay: 1s; }
        .detailsTextCont {
            position: relative;
            z-index: 2;
            position: absolute;
            width: 90%;
            max-height: 80vh;
            // transition: ease-in
            // transition-duration: 1s
            display: flex;
            opacity: 0;
            flex-direction: column; }
        .opacity {
            position: relative;
            opacity: 1;
            .detailsArrowLeft {
                object-fit: contain;
                width: 2em;
                cursor: pointer;
                opacity: 0;
                transition: ease-in;
                transition-duration: 1s; }
            .opacity {
                opacity: 1; }
            .detailsText {
                width: 100%;
                height: 100%;
                padding: 2em;
                display: flex;
                flex-direction: column;
                background-color: #fff;
                line-height: 1.5;
                transition: ease-in;
                transition-duration: 1s;
                // opacity: 0
                // -webkit-box-shadow: inset 0px -22px 19px 0px rgba(255,255,255,1)
                // -moz-box-shadow: inset 0px -22px 19px 0px rgba(255,255,255,1)
                // box-shadow: inset 0px -22px 19px 0px rgba(255,255,255,1)
                margin-top: 1em;
                .detailsTextTitle {
                    font-weight: bold;
                    font-size: 1.4em;
                    margin-bottom: 1em;
                    align-self: center;
                    text-transform: capitalize;
                    // transition: ease-in
 }                    // transition-duration: 1s
                .detailsTextContent {
                    overflow-y: scroll;
                    font-size: 1em;
                    width: 100%;
                    height: 100%;
                    transition: ease-in;
                    -ms-overflow-style: none;
                    scrollbar-width: none;
                    transition-duration: 1s;
                    // height: 80vh
                    // background-color: red
                    // padding: 1em
                    // white-space: nowrap
                    // text-overflow: ellipsis
                    &::-webkit-scrollbar {
                        display: none; } } } }
        .letsGoCont {
            position: absolute;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
            width: 100%;
            padding: 2em 2em;
            height: 80vh;
            z-index: -1;
            opacity: 0; }
        .opacity {
            z-index: 3;
            position: relative;
            opacity: 1;
            .letsGoContent {
                padding: 1em 1em 1em 0;
                background-color: #fff;
                border-radius: 2em;
                height: 80vh;
                width: 25em;
                color: #0008;
                -webkit-box-shadow: 0px 0px 16px 12px rgba(0,0,0,0.40);
                -moz-box-shadow: 0px 0px 16px 12px rgba(0,0,0,0.40);
                box-shadow: 0px 0px 16px 12px rgba(0,0,0,0.40); } }
        .footer {
            width: 100%;
            position: relative;
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            padding: 1em;
            z-index: -2;
            opacity: 0; }
        .footerAnime {
            z-index: 3;
            animation: opacityAnime 1s;
            opacity: 1;
            .details {
                bottom: 100%;
                position: absolute;
                left: 7em;
                padding: 0.2em 1.3em;
                text-transform: capitalize;
                font-weight: bold;
                font-size: 1.2em;
                cursor: pointer;
                color: #ffffff;
                border: 0.15em solid #ffffff;
                border-radius: 1em;
                transition: ease-in;
                transition-duration: 0.3s;
                &:hover {
                    color: #0e0e8e;
                    background-color: #fff; } }
            .footerLeft {
                display: flex;
                align-items: flex-end;
                .contactCont {
                    display: flex;
                    flex-direction: column;
                    .iconsCont {
                        position: relative; } }
                .downloadCont {
                    position: relative;
                    display: flex;
                    color: #fff;
                    text-transform: capitalize;
                    margin-left: 2em; }
                .icons {
                    object-fit: contain;
                    width: 1.5em;
                    padding: 0.2em; } }
            .langCont {
                color: #fff;
                display: flex;
                div {
                    text-transform: uppercase;
                    padding-left: 0.5em;
                    position: relative;
                    cursor: pointer;
                    &:nth-child(1) {
                        padding-right: 0.5em;
                        border-right: 2px solid #fff5; }
                    &:nth-child(2) {
                        padding-right: 0.5em;
                        border-right: 2px solid #fff5; } } } } }
    .hide {
        display: none; }
    .opacity {
        opacity: 1; }
    @keyframes contAnime {
        0% {
            opacity: 0;
 }            // width: 70%
        100% {
            // width: 100%
            opacity: 1; } }

    @keyframes background-animation {
        from {
            z-index: 5; }
        to {
            z-index: 1; } }
    @keyframes clouds-animation {
        from {
            top: -100%; }
        to {
            top: 0; } }
    @keyframes moon-animation {
        from {
            bottom: 100%; }
        to {
            bottom: 7em; } }
    @keyframes mosque-animation {
        from {
            // z-index: -2
            bottom: -100%; }
        to {
            // z-index: 2
            bottom: 0em; } }
    @keyframes towers-animation {
        from {
            bottom: -100%; }
        to {
            bottom: 0em; } }
    @keyframes opacityAnime {
        0% {
            opacity: 0; }
        100% {
            opacity: 1; } } }
@media screen and ( min-width: 1500px ) {
    .mainCont {
        .cont {
            .mosqueTowersCont {
                .moon {
                    bottom: 12em; }
                .mosque {
                    bottom: 5em; }
                .towers {
                    bottom: 5em; } }
            .littleTowers {
                bottom: 5em; } } } }
@media screen and ( max-width: 1100px ) {
    .mainCont {
        .cont {
            .mosqueTowersCont {
                // right: 0
                // width: 10em
                .moon {
                    // margin: 0 0.6em
                    // width: 9em
                    bottom: 9em; }
                .mosque {
                    // width: 7em
                    // margin: 0 1.5em
                    bottom: 9em; }
                .towers {
                    bottom: 9em; } }
            .littleTowers {
                // width: 15em
                bottom: 6em; }
            .letsGoCont {
                .letsGoContent {
                    width: 100%;
                    height: 70vh; } } } } }
@media screen and ( max-width: 800px ) {
    .mainCont {
        .cont {
            .mosqueTowersCont {
                right: 0;
                // width: 10em
                .moon {
                    // margin: 0 0.6em
                    // width: 9em
                    bottom: 9em; }
                .mosque {
                    // width: 27em
                    // margin: 0 1.5em
                    bottom: 2em; }
                .towers {
                    bottom: 0em; } }
            .littleTowers {
                // width: 15em
                bottom: 6em; }
            .letsGoCont {
                .letsGoContent {
                    width: 100%;
                    height: 70vh; } } } } }
